import axios from 'axios'

export const listCoupons = async(authtoken)=>{
    return await axios.get(`http://192.168.43.118:4000/api/coupons`, {headers:{authtoken}})
}

export const readCoupon = async(authtoken, slug)=>{
    return await axios.get(`http://192.168.43.118:4000/api/coupon/${slug}`, {headers:{authtoken}})
}

export const createCoupon = async(authtoken, coupon)=>{
    return await axios.post(`http://192.168.43.118:4000/api/coupon`, {coupon}, {
        headers:{
            authtoken
        }
    })
}

export const updateCoupon = async(authtoken, coupon, slug)=>{
    return await axios.put(`http://192.168.43.118:4000/api/coupon/${slug}`, {coupon}, {
        headers:{authtoken}
    })
}

export const removeCoupon = async(authtoken, slug)=>{
    return await axios.delete(`http://192.168.43.118:4000/api/coupon/${slug}`, {
        headers:{authtoken}
    })
}

export const applyCoupon = async(authtoken, title)=>{
    return await axios.post(`http://192.168.43.118:4000/api/coupon/apply`, title, {
        headers:{authtoken}
    })
}