import axios from 'axios'

export const createWishlist = async(authtoken, productId)=>{
    return await axios.post(`http://192.168.43.118:4000/api/wishlist`, {productId},{
        headers:{
            authtoken
        }
    })
}

export const listWishlist = async(authtoken)=>{
    return await axios.get(`http://192.168.43.118:4000/api/wishlist`,{
        headers:{
            authtoken
        }
    })
}

export const removeWishlist = async(authtoken, id)=>{
    return await axios.put(`http://192.168.43.118:4000/api/wishlist/${id}`, {},{
        headers:{
            authtoken
        }
    })
}

export const updateNewsletter = async(authtoken, newsletter)=>{
    return await axios.put(`http://192.168.43.118:4000/api/newsletter`, {newsletter}, {
        headers:{
            authtoken
        }
    })
}