import axios from 'axios'

export const listCategories = async()=>{
    return await axios.get('http://192.168.43.118:4000/api/category')
}

export const readCategory = async(slug)=>{
    return await axios.get(`http://192.168.43.118:4000/api/category/${slug}`)
}

export const createCategory = async(authtoken, name)=>{
    return await axios.post('http://192.168.43.118:4000/api/category', {name}, {
        headers:{authtoken}
    })
}

export const updateCategory = async(authtoken, slug, name)=>{
    return await axios.put(`http://192.168.43.118:4000/api/category/update/${slug}`, {name}, {
        headers:{authtoken}
    })
}

export const removeCategory = async(authtoken, slug)=>{
    return await axios.delete(`http://192.168.43.118:4000/api/category/${slug}`, {headers: { authtoken}})
}

export const listSubOnCategory = async(id)=>{
    return await axios.get(`http://192.168.43.118:4000/api/category/sub/${id}`)
}