import axios from 'axios'

export const listProducts = async(sort, order, page)=>{
    return await axios.post(`http://192.168.43.118:4000/api/products`,{sort, order, page} )
}

export const listProductsCount= async()=>{
    return await axios.get(`http://192.168.43.118:4000/api/products/count`)
}
export const readProduct = async(slug)=>{
    return await axios.get(`http://192.168.43.118:4000/api/product/${slug}`)
}

export const createProduct = async(authtoken, body)=>{
    return await axios.post(`http://192.168.43.118:4000/api/product`, body, {
        headers:{authtoken}
    })
}

export const updateProduct = async(authtoken, slug, body)=>{
    return await axios.put(`http://192.168.43.118:4000/api/product/update/${slug}`, body, {
        headers:{authtoken}
    })
}

export const removeProduct = async(authtoken, slug)=>{
    return await axios.delete(`http://192.168.43.118:4000/api/product/${slug}`, {
        headers:{authtoken}
    })
}

export const listRelatedProducts = async(productId)=>{
    return await axios.get(`http://192.168.43.118:4000/api/product/related/${productId}`)
}

export const filterProducts = async(query)=>{
    return await axios.post(`http://192.168.43.118:4000/api/product/filters`,query)
}

export const rateProduct = async(authtoken,id, star, comment)=>{
    return await axios.post(`http://192.168.43.118:4000/api/product/rate/${id}`,{star, comment}, {
        headers:{authtoken}
    })
}
