import axios from 'axios'

export const userCart = async(authtoken, cart)=>{
    return await axios.post(`http://192.168.43.118:4000/api/user/cart`, {cart},{
        headers:{authtoken}
    })
}

export const listUserCart = async(authtoken)=>{
    return await axios.get(`http://192.168.43.118:4000/api/user/cart/list`, {
        headers:{authtoken}
    })
}

export const removeUserCart = async(authtoken)=>{
    return await axios.delete(`http://192.168.43.118:4000/api/user/cart/remove`, {
        headers:{authtoken}
    })
}

export const userAddress = async(authtoken, address)=>{
    return await axios.post(`http://192.168.43.118:4000/api/user/address`, {address},{
        headers:{authtoken}
    })
}

export const listUserAddress = async(authtoken)=>{
    return await axios.get(`http://192.168.43.118:4000/api/user/address/list`, {
        headers:{authtoken}
    })
}

export const createOrderWithCOD = async(authtoken, coupon)=>{
    return await axios.post(`http://192.168.43.118:4000/api/order/cod`, {coupon},{
        headers:{authtoken}
    })
}


