import axios from 'axios'

export const listSubs = async()=>{
    return await axios.get(`http://192.168.43.118:4000/api/subs`)
}

export const readSub = async(slug)=>{
    return await axios.get(`http://192.168.43.118:4000/api/sub/${slug}`)
}

export const createSub = async(authtoken, body)=>{
    return await axios.post(`http://192.168.43.118:4000/api/sub`, body, {
        headers:{authtoken}
    })
}

export const updateSub = async(authtoken, body, slug)=>{
    return await axios.put(`http://192.168.43.118:4000/api/sub/update/${slug}`, body, {
        headers:{
            authtoken
        }
    })
}

export const removeSub = async(authtoken, slug)=>{
    return await axios.delete(`http://192.168.43.118:4000/api/sub/${slug}`, {
        headers:{authtoken}
    })
}