import axios from 'axios'

export const createOrUpdateUser = async(authtoken)=>{
    return await axios.post(`http://192.168.43.118:4000/api/create-or-update-user`, {},{
        headers:{
            authtoken
        }
    })
}

export const currentUser = async(authtoken)=>{
    return await axios.post(`http://192.168.43.118:4000/api/current-user`, {},{
        headers:{
            authtoken
        }
    })
}
