import axios from 'axios'

export const getAllOrders = async(authtoken, sort)=>{
    return await axios.post(`http://192.168.43.118:4000/api/orders`,{sort},{
        headers:{authtoken}
    })
}

export const readOrder = async(authtoken, id)=>{
    return await axios.get(`http://192.168.43.118:4000/api/order/${id}`,{
        headers:{authtoken}
    })
}

export const updateOrder = async(authtoken, status, id)=>{
    return await axios.put(`http://192.168.43.118:4000/api/order/update/${id}`,{status},{
        headers:{authtoken}
    })
}

export const getUserOrders = async(authtoken)=>{
    return await axios.get(`http://192.168.43.118:4000/api/user/orders`,{
        headers:{authtoken}
    })
}
